$fonts_dir: "app/fonts";

@font-face {
  font-family: 'gt_haptiklight';
  src: url('#{$fonts_dir}/gt-haptik-light-webfont.eot');
}

@font-face {
  font-family: 'gt_haptiklight';
  src: url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'),
    url(data:application/font-woff;charset=utf-8;base64,) format('woff'),
    url('#{$fonts_dir}/gt-haptik-light-webfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}


